<script>
    import PopUp from "./PopUp.svelte";
    import { scrollPositionStore } from "../../lib/stores.js";
    import { random } from "../../lib/utils.js";

    export let data;
    export let section;
    export let backgroundY;
    export let backgroundWidth;
    export let userSelect;
    export let popupIds;

    const absoluteBackgroundY = $scrollPositionStore + backgroundY;
    $: relativeY = $scrollPositionStore - absoluteBackgroundY;
    $: console.log(relativeY);

    function generateLeft() {
        let orignalLeft = random(0, backgroundWidth-220, 0);
        return orignalLeft;
    }

    function generateTop() {
        return random(0, 100, 0);
    }

</script>

{#if section === "section01"}
    {#each popupIds as id}
        <PopUp
            {data}
            {relativeY}
            {userSelect}
            id={id}
            top={generateTop()}
            left={generateLeft()}
            modifier={random(0.1, 0.6, 2)}
        />
    {/each}
{/if}
